h1,
h2,
h3 {
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.45);
}

.divider {
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.divider::after {
  background: linear-gradient(to left, rgba(255, 255, 255, 0.3), #000);
  margin-left: 4vh;
}

.divider::before {
  background: linear-gradient(to right, #ffffff, #000);
  margin-right: 4vh;
}

.divider::after,
.divider::before {
  content: "";
  display: block;
  height: 0.09em;
  min-width: 30vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 1.875rem;
  line-height: 2.25rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.invisible {
  visibility: hidden;
  width: 0 !important;
  height: 0 !important;
}

.p-card-title {
  text-align: center;
}

.login-register-card {
  flex: 0.34;
}

.showcase::after {
  content: "";
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("Images/sxoleio.jpg");

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  filter: blur(2px);
}

